.hideBtn {
    color: #fff;
    background-color: #ff4051;
    border: 1px solid transparent;
   font-weight: 500;
    font-size: 14px;
    line-height: 1.3
}
.hideBtn:hover {
    background-color: #e23442;
    border-color: #e23442;
    color: #fff;
}

.erro_204_fix{
    box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 10px; 
    max-width: 100%; 
    width: 100%; 
    transform: none; 
    bottom: 0px; 
    z-index: 9998; 
    position: fixed; 
    padding: 15px; 
    border-radius: 0px; 
    text-align: center; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    background: rgb(26, 32, 41);
}

.noPostContent .erro_204_fix{
    box-shadow: none; 
    background: none;
    position: relative;

}


.erro_204_fix h2{ 
    font-size: 25px;
    font-weight: 600;
    margin: 0 15px 0px 0;
    color: #fff;
    text-transform: capitalize;
}

.erro_204_fix p{
    font-size: 16px;
    color: #fff;
    font-weight: 400;
    margin-bottom: 0px;
}

.erro_204_fix p a{
    color: #c20e5e;
    border-bottom: 1px dashed #c20e5e;
    transition: all ease .3s;
}